}

/* Normal */
@font-face {
  font-family: SFNSDisplay;
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/SFNSDisplay") format("ttf"); }

body {
  font-family: 'Lucida Grande'; }

.today-stats {
