.tempC,
.tempF {
  position: absolute;
  right: 0;
  top: -43px;
  transition: all 0.3s ease; }

.show-farenheightFormat .tempF {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.show-farenheightFormat .tempC {
  opacity: 0;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px); }

.hide-farenheightFormat .tempF {
  opacity: 0;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px); }

.hide-farenheightFormat .tempC {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.today-weather {
  position: absolute;
