.forecast-week-wrapper {
  margin-bottom: 0;
  position: relative;
  z-index: 0;
  align-items: stretch;
  width: 100%;
  position: relative; }
  .forecast-week-wrapper:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: #ffffff;
    z-index: 20;
    opacity: 1;
    transition: all 0.6s ease; }
  .forecast-week-wrapper.data-mounted:after {
    opacity: 0; }
  .forecast-week-wrapper.data-mounted .forecast-day-info {
    opacity: 1; }
  .forecast-week-wrapper .forecast-day-info {
    opacity: 0; }
    .forecast-week-wrapper .forecast-day-info:last-child {
