  position: relative; }
  .forecast-temp-wrapper .tempC {
    margin-top: -36px; }
  .forecast-temp-wrapper .tempC,
  .forecast-temp-wrapper .tempF {
    position: relative;
    top: inherit;
    display: block; }
    .forecast-temp-wrapper .tempC:after,
    .forecast-temp-wrapper .tempF:after {
      content: "";
