      height: 6px;
      border: 2px solid #4A4A4A;
      border-radius: 50%;
      display: block;
      top: 13px;
      position: absolute;
      right: -7px; }

.day-stats {
  background: #ffffff;
  font-family: 'Lucida Grande';
  font-size: 14px;
  color: #4A4A4A;
  min-height: 60px; }
  .day-stats span,
  .day-stats .icon-wrapper {
    display: inline-block; }
  .day-stats .icon-wrapper,
  .day-stats .forecast-temp-wrapper {
    float: right;
    text-align: right;
    margin-left: 10px; }

.btn.btn-custom {
  background: #65AED5;
  color: #ffffff;
  border-radius: 20px;
  font-size: 14px;
  padding: 2px 10px;
  display: inline-block;
  margin-right: 5px;
  top: 0;
  position: relative;
  vertical-align: top;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 0.3s ease; }
  .btn.btn-custom:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    color: #ffffff; }

.button-toggle-temp {
  position: relative;
  border-radius: 20px;
  padding: 0px 7px;
  background: #65AED5;
  width: 50px; }
  .button-toggle-temp .toggle {
    content: "";
    position: absolute;
    background: #ffffff;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    top: 2px;
    left: 2px;
    -webkit-transform: translateX(23px);
            transform: translateX(23px);
    transition: all 0.3s ease; }
  .button-toggle-temp .tempOptionF,
  .button-toggle-temp .tempOptionC {
    display: inline-block;
    position: relative;
    transition: all 0.3s ease;
    z-index: 2;
    margin-left: -1px; }
    .button-toggle-temp .tempOptionF:after,
    .button-toggle-temp .tempOptionC:after {
      content: "";
      width: 3px;
      height: 3px;
      border: 1px solid #ffffff;
      border-radius: 50%;
      display: block;
      top: 8px;
      position: absolute;
      right: -3px; }
  .button-toggle-temp .tempOptionF {
    color: #65AED5;
    float: right; }
    .button-toggle-temp .tempOptionF:after {
      border-color: #65AED5; }
  .button-toggle-temp .tempOptionC {
    color: #ffffff;
    float: left; }
    .button-toggle-temp .tempOptionC:after {
      border-color: #ffffff; }

.hide-farenheightFormat .button-toggle-temp .toggle {
  -webkit-transform: translateX(0px);
          transform: translateX(0px); }

.hide-farenheightFormat .button-toggle-temp .tempOptionF {
  color: #ffffff; }
  .hide-farenheightFormat .button-toggle-temp .tempOptionF:after {
    border-color: #ffffff; }

.hide-farenheightFormat .button-toggle-temp .tempOptionC {
  color: #65AED5; }
  .hide-farenheightFormat .button-toggle-temp .tempOptionC:after {
    border-color: #65AED5; }

.app-actions-wrapper {
  position: absolute;
  right: 0;
  top: 0;
  margin: 30px 20px;
  z-index: 30; }

.form-wrapper {
  position: absolute;
  z-index: 40;
  top: 160px;
  left: 0;
  right: 0;
  background: white;
  box-shadow: 0 0 20px 0px grey;
  padding: 20px;
  transition: all 0.3s ease;
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
  -webkit-transform-origin: top;
          transform-origin: top;
  opacity: 0;
  margin: 10px; }

.overlay {
  background: rgba(27, 79, 97, 0.6);
  opacity: 0;
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease;
  z-index: 25; }

.show-location-form .form-wrapper {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  opacity: 1; }

.show-location-form .overlay {
  display: block;
  opacity: 1; }

.icon-wrapper {
  display: inline-block; }

.cloud1,
.cloud2,
.cloud3,
.cloud4 {
  position: absolute;
  z-index: 30; }

.cloud1 {
  right: -50px;
  width: 208px;
  top: 70px;
  -webkit-animation: Clouds-roll-by infinite 170s linear;
          animation: Clouds-roll-by infinite 170s linear; }

.cloud2 {
  left: -80px;
  width: 146px;
  top: 146px;
  -webkit-animation: Clouds-roll-by2 infinite 200s linear;
          animation: Clouds-roll-by2 infinite 200s linear; }

.cloud3 {
  left: 800px;
  width: 120px;
  top: 180px;
  opacity: .85;
  -webkit-animation: Clouds-roll-by3 infinite 240s linear;
          animation: Clouds-roll-by3 infinite 240s linear; }

.cloud4 {
  right: 800px;
  width: 130px;
  top: 85px;
  opacity: .8;
  -webkit-animation: Clouds-roll-by4 infinite 140s linear;
          animation: Clouds-roll-by4 infinite 140s linear; }

.App {
  background: #115DA3;
  background-image: linear-gradient(-46deg, #4DCEEC 0%, #115DA3 100%);
  padding: 20px;
  position: relative;
  text-align: center; }

.App-header {
