          filter: blur(2px); }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes Clouds-roll-by {
  0% {
    -webkit-transform: translateX(-70px);
            transform: translateX(-70px); }
  30% {
    -webkit-transform: translateX(-800px);
            transform: translateX(-800px); }
  70% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px); }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@keyframes Clouds-roll-by {
  0% {
    -webkit-transform: translateX(-70px);
            transform: translateX(-70px); }
  30% {
    -webkit-transform: translateX(-800px);
            transform: translateX(-800px); }
  70% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px); }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@-webkit-keyframes Clouds-roll-by2 {
  0% {
    -webkit-transform: translateX(120px);
            transform: translateX(120px); }
  30% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px); }
  70% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px); }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@keyframes Clouds-roll-by2 {
  0% {
    -webkit-transform: translateX(120px);
            transform: translateX(120px); }
  30% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px); }
  70% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px); }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@-webkit-keyframes Clouds-roll-by3 {
  0% {
    -webkit-transform: translateX(30px) scaleX(-1);
            transform: translateX(30px) scaleX(-1); }
  30% {
    -webkit-transform: translateX(1000px) scaleX(-1);
            transform: translateX(1000px) scaleX(-1); }
  70% {
    -webkit-transform: translateX(-800px) scaleX(-1);
            transform: translateX(-800px) scaleX(-1); }
  100% {
    -webkit-transform: translateX(0px) scaleX(-1);
            transform: translateX(0px) scaleX(-1); } }

@keyframes Clouds-roll-by3 {
  0% {
    -webkit-transform: translateX(30px) scaleX(-1);
            transform: translateX(30px) scaleX(-1); }
